<template>
<div class="container">
    <h1>Login</h1>
    <form @submit.prevent="login">
        <input type="email" placeholder="Email" class="form-control my-2" v-model="usuario.email">
        <input type="password" placeholder="Contrase침a" class="form-control my-2" v-model="usuario.pass">
        <b-button class="col-md-5 bg-primary col-sm-12" type="submit">Acceder</b-button>
    </form>
    <div v-if="mensaje!=''">
        <p>{{mensaje}}</p>
    </div>
</div>
</template>


<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
    data(){
        return{
            usuario: {email: '',pass: ''},
            mensaje: ''
        }
    },
     methods:{
    ...mapMutations(['obtenerUsuario']),
    ...mapActions(['guardarUsuario', 'leerToken', 'cerrarSesion']),
    login(){
      this.axios.post('/login', this.usuario)
        .then(res => {
          // console.log(res.data.token);
          const token = res.data.token;
          // this.usuarioDB = res.data.usuarioDB
          this.guardarUsuario(token);
        })
        .catch(err => {
          console.log(err.response.data.mensaje);
          this.mensaje = err.response.data.mensaje;
        })
    }
  }
}
</script>